import {model} from './model'
import {title, text, columns, image} from './function'

const _site = document.querySelector("#site");
model.forEach((block) => {
  let html = ""; //выполнение за функцией приведет к ошибке
  if (block.type === "title") {
    //строгое сравнение, тип не изменяется
    html = title(block);
  } else if (block.type === "text") {
    html = text(block);
  }
  else if(block.type === 'columns'){
    html = columns(block)
  }
  else if(block.type === 'image'){
      html = image(block)
  }

  _site.insertAdjacentHTML('beforeend', html) //вставляет в html, работет быстрее innerHTML
});


