export function title(block){ //function - можем вызвать до ее реализации
    return `
    <div class="row">
    <div class="col">
        <h1>${block.value}</h1>
    </div>
</div> 
    `
}
export function text(block){
    return `
    <div class="row">
    <div class="col">
        <p>${block.value}</p>
    </div>
</div>
    `
}
export function columns(block){
    //#1
    /*let html = ''
    block.value.forEach(item => {
        html += `
        <div class="col">
            ${item}
        </div>
        `
    })*/
    //2
    const html = block.value.map(item => `<div class="col">${item}</div>`)
    return`
    <div class="row">
          ${html.join('') /*join обьединяет все элементы array в одну строку*/}   
    </div>
    `
}
export function image(block){
    return`
        <div class="row">
            <img src="${block.value}"/>
        </div>
    `
}